@import "./colors.scss";
@import "./mediaquery.scss";

.totalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.centerLeft {
  @extend .totalCenter;
  align-items: flex-start;
}

.centerRight {
  @extend .totalCenter;
  align-items: flex-end;
}

.columnFlex {
  display: flex;
  flex-direction: column;
}

.rowFlex {
  display: flex;
  flex-direction: row;
}

.wrapper {
  width: 80%;
  padding-top: 30px;
  padding-bottom: 30px;

  @include for-phone-only {
    width: 90%;
  }
}

.primary-btn {
  border: none;
  background-color: transparent;
  color: $text-color;
  opacity: 0.5;
  padding: 3px 5px;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.3s;

  &:hover {
    cursor: pointer;
    opacity: 1;
  }
}

.secondary-btn {
  border: 1px solid $text-color;
  background-color: $text-color;
  color: black;
  border-radius: 20px;
  font-size: 14px;
  padding: 3px 5px;
  font-weight: 600;
  transition: all 0.3s;

  &:hover {
    cursor: pointer;
    background-color: $clicked-btn;
  }

  @include for-phone-only {
    transition: none;

    &:hover {
      background-color: $text-color;
    }
  }
}

a {
  color: $text-color;
  text-decoration: none;
}

li {
  list-style: none;
}

.hover {
  &:hover {
    cursor: pointer;
  }
}

.none {
  display: none;
}
